import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import Header from './header'
import './dashboard.css';
import Loader  from '../loader';
import axios from 'axios';
export default class RestaurantDashboard extends Component {
    state = {
        restaurantName: localStorage.getItem('restaurant'),
        reservations: [],
        feedbacks: [],
        numbers: [],
        customers: 0,
        loading: 'block'
    }

    componentDidMount(){
        var resv = [], 
            resv2 = [],
            list = [],
            numList = [],
            dis = this, 
            rest = localStorage.getItem('restaurant');
            if(rest === null){
                window.location.href= '/login/restaurant'
            }

        axios.get('http://18.223.252.52:5000/api/feedbacks')
        .then( res => {
            res.data.data.map( elem => {
                if(elem.restaurantName === rest){
                    resv2.push(elem);
                }
            })
            dis.setState({ feedbacks: resv2})
        })
        
        axios.get('http://18.223.252.52:5000/api/getCustomers')
        .then( res => {
            res.data.data.map( elem => {
                numList.push(elem)
                return true
            })
            dis.setState({ numbers: numList});

            numList.map( no => {
                axios.post('http://18.223.252.52:5000/api/customerByRestaurant', {phone: no, restaurantName: rest})
                .then( res => {
                    
                    res.data.data.map( elem => {
                        if(res.data.status){
                            list.push(elem)
                        }
                        return true
                    })
                    
                    // var list = [];
                    setTimeout(() => {
                        var dataArr = list.map(item=>{
                            return [item.phone,item]
                        }); 
                        var maparr = new Map(dataArr);
                        
                        var result = ([...maparr.values()]).length;        

                        dis.setState({ customers: result})
                    }, 1000);

                    return true
                })
                return true
            })
            return true
        })

        
        
        // dis.setState({ customers: list.length})

        axios.get('http://18.223.252.52:5000/api/reservations')
        .then( res => {
            res.data.data.map( elem => {
                
                var rest = dis.state.restaurantName;
                

                if(elem.restaurantName === rest){
                    resv.push(elem);
                }else{
                    resv = [...resv];
                }
                return true
            })
            dis.setState({ reservations: resv})
        })



        
        


        setTimeout(() => {
            this.setState({ loading: 'none'})
        }, 2000); 
    }

    render() {
        return (
            <div>
                <Loader status={this.state.loading} /> 
                <Header resName={this.state.restaurantName} />
                <div className="container">
                    <div className="row mb-4">
                        <div className="col-md-3">
                            <div className="rs-report-box">
                                <i className="ti-direction"></i>
                                <div>
                                    <label>Reservations</label>
                                    <h2>{this.state.reservations.length}</h2>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-3">
                            <div className="rs-report-box">
                                <i className="ti-face-smile"></i>
                                <div>
                                    <label>Customers</label>
                                    <h2>{this.state.customers}</h2>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-3">
                            <div className="rs-report-box">
                                <i className="ti-comments"></i>
                                <div>
                                    <label>Feedbacks</label>
                                    <h2>{this.state.feedbacks.length}</h2>
                                </div>
                            </div>
                        </div>
                    </div>    
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col-md-4">
                            <div className="border py-4 text-center rounded border-primary">
                                <img src="/assets/images/search-reservation.jpg" className="rs-item-img" alt="" />
                                <div>
                                    <h4 className="rs-item-title">Our Reservations</h4>
                                    {/* <p className="rs-item-desc">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p> */}
                                    <Link to="/restaurant/search-reservation" className="btn btn-primary rs-item-btn">Search Now</Link>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-4">
                            <div className="border py-4 text-center rounded border-primary">
                                <img src="/assets/images/check-reservation.jpg" className="rs-item-img" alt="" />
                                <div>
                                    <h4 className="rs-item-title">Our Customers</h4>
                                    {/* <p className="rs-item-desc">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p> */}
                                    <Link to="/restaurant/customers" className="btn btn-primary rs-item-btn">See All</Link>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-4">
                            <div className="border py-4 text-center rounded border-primary">
                                <img src="/assets/images/feedback.jpg" className="rs-item-img" alt="" />
                                <div>
                                    <h4 className="rs-item-title">Customer Feedback</h4>
                                    {/* <p className="rs-item-desc">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p> */}
                                    <Link to="/restaurant/feedback" className="btn btn-primary rs-item-btn">View feedback</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
