import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export default class AdminSidebar extends Component {
    state = {
        activeLinks: this.props.activeLink
    }
    render() {
        return (
            <aside className="rs-sidebar">
                <ul className="list-unstyled rs-side-menu">
                    <li>
                        <Link to="/admin/dashboard" className={(this.state.activeLinks === "dashboard") ? "active" : ''}>
                            <i className="ti-view-grid"></i>
                            <span>Dashboard</span>
                        </Link>
                    </li>
                    <li>
                        <Link to="/admin/restaurants" className={(this.state.activeLinks === "restaurants") ? "active" : ''}>
                            <i className="ti-star"></i>
                            <span>Restaurants</span>
                        </Link>
                    </li>
                    <li>
                        <Link to="/admin/restaurant-selection" className={(this.state.activeLinks === "reservation") ? "active" : ''}>
                            <i className="ti-bookmark"></i>
                            <span>Reservations</span>
                        </Link>
                    </li>
                    {/* <li>
                        <Link to="/admin/reservation/feedback" className={(this.state.activeLinks === "feedback") ? "active" : ''}>
                            <i className="ti-comment-alt"></i>
                            <span>Feedback</span>
                        </Link>
                    </li> */}
                </ul>
            </aside>
        )
    }
}
