import React, { Component } from 'react'
import AdminHeader from './AdminHeader'
import AdminSidebar from './AdminSidebar'
import './index.css'
import axios from 'axios'

export default class AdminDashboard extends Component {
    state = {
        reservations: 0,
        restaurants: 0,
        feedbacks: 0,
        customers: 0
    }
    componentDidMount(){
        var dis = this;
        axios.get('http://18.223.252.52:5000/api/restaurants')
        .then( res  => dis.setState({ restaurants: res.data.data.length}))
        axios.get('http://18.223.252.52:5000/api/reservations')
        .then( res  => dis.setState({ reservations: res.data.data.length}))
        axios.get('http://18.223.252.52:5000/api/feedbacks')
        .then( res  => dis.setState({ feedbacks: res.data.data.length}))
        axios.get('http://18.223.252.52:5000/api/getCustomers')
        .then( res  => dis.setState({ customers: res.data.data.length}))
        
    }
    render() {
        return (
            <div>
                <AdminHeader />
                <AdminSidebar activeLink="dashboard" />

                <div className="rs-content">
                    <div className="bg-gray vh-100 py-4 px-3">
                        <div className="container ">
                            <h2>Statistics</h2>
                            <div className="row justify-content-center">
                                <div className="col-md-3">
                                    <div className="rs-report-box">
                                        <i className="ti-tag"></i>
                                        <div>
                                            <label>Restaurant</label>
                                            <h2>{this.state.restaurants}</h2>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-3">
                                    <div className="rs-report-box">
                                        <i className="ti-direction"></i>
                                        <div>
                                            <label>Reservation</label>
                                            <h2>{this.state.reservations}</h2>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-3">
                                    <div className="rs-report-box">
                                        <i className="ti-face-smile"></i>
                                        <div>
                                            <label>Customer</label>
                                            <h2>{this.state.customers}</h2>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-3">
                                    <div className="rs-report-box">
                                        <i className="ti-comment-alt"></i>
                                        <div>
                                            <label>Feedback</label>
                                            <h2>{this.state.feedbacks}</h2>
                                        </div>
                                    </div>
                                </div>
                            </div>
                                                        
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
