import React, { Component } from 'react'
import AdminHeader from "./AdminHeader"
import AdminSidebar from "./AdminSidebar"
import {Link} from 'react-router-dom'
import axios from 'axios'
import $ from 'jquery'

import './index.css'

export default class ReservationList extends Component {
    state = {
        reservations : [],
        restaurantName: '',
        name: '',
        phone: '',
        date: '',
        time: '',
        rating: '',
        comment: '',
        imageURL: '',
        latestRating:'',
        latestComment: '',
        latestImage: '',
        alertText: '',
        alertClass:'alert-danger'
    }

    componentDidMount(){
        
        this.setState({restaurantName: localStorage.getItem('selected_restaurant')});

        axios.post('http://18.223.252.52:5000/api/reservationsByrestaurant', {restaurantName: this.state.restaurantName })
        .then(res => {
            this.setState({ reservations: res.data.data})
        });
        var dis = this;
        

        $('.number-arr span').click(function(){
            var rate = $(this).data('count');
            $(this).addClass('active').siblings('span').removeClass('active');
            dis.setState({rating: rate});
        })
    }

    componentDidUpdate(){
        axios.post('http://18.223.252.52:5000/api/reservationsByrestaurant', {restaurantName: this.state.restaurantName })
        .then(res => {
            this.setState({ reservations: res.data.data})
        });
    }

    handleUploadFile = e => {
        var txt = e.target.files.item(0).name;
        console.log(txt)
        $(e.target).next().find('span').text(txt);
        var input  = e.target;
        var dis = this;
        if (input.files && input.files[0]) {
            var reader = new FileReader();
    
            reader.onload = function (e) {
                dis.setState({imageURL: e.target.result});
            }
    
            reader.readAsDataURL(input.files[0]);
        }
    }

    handleFeedbackForm = e => {
        e.preventDefault()
        var data = {
            restaurantName: this.state.restaurantName,
            customerName: this.state.name,
            phone: this.state.phone,
            reservationDate: this.state.date,
            reservationTime: this.state.time,
            rating: this.state.rating, 
            image:  this.state.imageURL, 
            comment: this.state.comment
        };
        var dis= this;
        var customer = $(e.target).parent().parent('tr').find('td:nth-child(2)').text();
        axios.post('http://18.223.252.52:5000/api/sendFeedback', data)
        .then( res => {
            if(res.data.status){
                dis.setState({alertText: res.data.message, alertClass: 'alert-success' })
                $('.alert2').addClass('show').removeClass("d-none");

                var rest = localStorage.getItem('selected_restaurant');
                var msg = `${customer} has given a feedback of a reservation.`
                axios.post('http://18.223.252.52:5000/api/addNotification', {user: rest, message: msg})
                .then(res => { console.log('notification sent.')})
                setTimeout(function(){
                    window.location.reload()
                }, 1000)
            }
        })
    }

    handleData = e => {
        var cname = $(e.target).parent('td').data('name'),
            rdate = $(e.target).parent('td').data('date'),        
            rtime = $(e.target).parent('td').data('time'),
            cphone = $(e.target).parent('td').data('phone');

        this.setState({ 
            name: cname,
            phone: cphone, 
            date: rdate, 
            time: rtime
        })
    }

    handleViewData = e => {
        var cname = $(e.target).parent('td').data('name'),
            rdate = $(e.target).parent('td').data('date');
            var dis = this;
            axios.post('http://18.223.252.52:5000/api/getFeedback', {customerName: cname, reservationDate: rdate})
            .then( res => {
                console.log(res)
                if(res.data.status){
                    dis.setState({latestRating: res.data.data[0].rating, latestComment: res.data.data[0].comment, latestImage: res.data.data[0].image})
                }
            })
    }

    handlCancelReservation = e => {
        
        var resID = $(e.target).parent().parent('tr').data('id');
        var rest = localStorage.getItem('selected_restaurant');
        var customer = $(e.target).parent().parent('tr').find('td:nth-child(2)').text();
        axios.post('http://18.223.252.52:5000/api/activeReservation', { id: resID})
        .then(res => {
            if(res.data.status) {
                this.setState({alertText: "Reservation cancelled.", alertClass: 'alert-success' })
                $('.alert').addClass('show').removeClass("d-none");
                
                var msg = `${customer} has cancelled a reservation.`
                axios.post('http://18.223.252.52:5000/api/addNotification', {user: rest, message: msg})
                .then(res => { console.log('notification sent.')})
                setTimeout(() => {
                    window.location.reload()
                },1000)
            }
        })
    }

    filterRestaurant = e => {
        var data = e.target.value;
        
        $('.table tbody > tr td:nth-child(6)').each(function(){
            var dis = $(this);
            var dvalue = $(this).text();
            if(dvalue !== data && data !== ''){
                dis.parent().hide()
            }else{
                dis.parent().show()
            }
        })
    }

    clearFilter = e => {
        $('.table tr').show()
    }

    render() {
        return (
            <div>
                <AdminHeader />
                <AdminSidebar activeLink="reservation" />
                <div className="rs-content">
                    <div className="d-flex justify-content-between align-items-center">
                        <h4 className="form-title">{this.state.restaurantName} - Reservations</h4>
                        <Link to="/admin/reservation/add" className="btn btn-sn btn-outline-primary shadow me-5 mt-4"><i className="ti-plus me-2"></i>Add New Reservation</Link>
                    </div>
                    <hr/>
                    <div className="container pb-4  px-5">
                        <div className={`alert fade d-none ${this.state.alertClass}`}>{this.state.alertText}</div>
                        <div className="d-flex justify-content-end align-items-center">

                        <input type='date' className="form-control d-inline-block" onChange={this.filterRestaurant} style={{maxWidth: 180}} />
                        <button className="btn btn-info text-white rounded ms-2" onClick={this.clearFilter}>&times; clear filter</button>
                        </div>
                        <table className="table mt-0 table-hover">
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>Customer Name</th>
                                    <th>Phone</th>
                                    <th>No of Guest</th>
                                    <th>Cuisine</th>
                                    <th>Date</th>
                                    <th>Time</th>
                                    <th>Feedback</th>
                                    <th>Status</th>
                                    <th>Action</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    (this.state.reservations.length > 0) ?
                                    this.state.reservations.map((el,index) => 
                                        <tr key={el._id} data-id={el._id}>
                                            <td>{index + 1}.</td>
                                            <td>{el.customerName}</td>
                                            <td>{el.phone}</td>
                                            <td>{el.guestCount}</td>
                                            <td>{el.cuisine}</td>
                                            <td>{el.reservationDate.split('T')[0]}</td>
                                            <td>{el.reservationTime}</td>
                                            <td 
                                                data-rname={el.restaurantName} 
                                                data-name={el.customerName} 
                                                data-phone={el.phone} 
                                                data-date={el.reservationDate} 
                                                data-time={el.reservationTime} 
                                            >{(el.feedStatus === "waiting") ? <button className="btn btn-sm btn-secondary badge p-2" data-bs-target="#send-feedback" data-bs-toggle="modal" onClick={this.handleData}><i className="ti-comment me-1"></i>Send</button> : <button className="btn bt-sm btn-success badge p-2"  onClick={this.handleViewData} data-bs-target="#view-feedback" data-bs-toggle="modal"><i className="ti-eye me-1"></i>View</button> }</td>
                                            {(el.status === "waiting") 
                                            ? <td><button className="btn btn-sm text-dark badge btn-warning shadow p-2">{el.status}</button></td> 
                                            : (el.status === "confirmed") 
                                            ? <td><button className="btn btn-sm btn-primary badge p-2">{el.status}</button></td> 
                                            : (el.status === "Seated In")
                                            ? <td><button className="btn btn-sm bg-violet text-white badge p-2">{el.status}</button></td>
                                            : <td><button className="btn btn-sm btn-danger badge p-2">{el.status}</button></td>
                                            
                                            }
                                            <td>
                                                {
                                                    (el.isActive) ? 
                                                    <span 
                                                        className="btn btn-sm shadow btn-danger" 
                                                        onClick={this.handlCancelReservation}
                                                    >Cancel
                                                    </span>
                                                    : 
                                                    <span 
                                                        className="btn btn-sm shadow btn-danger"
                                                        disabled
                                                    >Cancelled
                                                    </span>
                                                }
                                            </td>
                                            
                                        </tr>    
                                    )
                                    : <tr> <td colSpan="10" className="text-center">Searching...</td></tr>
                                }
                            </tbody>
                        </table>
                        {/* <div  className="d-flex justify-content-between">
                            <p>10 Record for 1 of 4</p>
                            <ul className="pagination">
                                <li className="page-item"><Link className="page-link" to="#">Previous</Link></li>
                                <li className="page-item"><Link className="page-link" to="#">1</Link></li>
                                <li className="page-item"><Link className="page-link" to="#">2</Link></li>
                                <li className="page-item"><Link className="page-link" to="#">3</Link></li>
                                <li className="page-item"><Link className="page-link" to="#">Next</Link></li>
                            </ul>
                        </div> */}
                    </div>
                </div>

                <div className="modal fade slide" id="send-feedback">
                    <div className="modal-dialog">
                        <div className="modal-content px-4 py-2">
                            <div className="modal-header border-bottom-0">
                                <h4 className="modal-title">Feedback</h4>
                                <button type="button" className="btn-close" data-bs-dismiss="modal" 
                                aria-label="Close"></button>
                            </div>
                            <div className="modal-body">
                                <form onSubmit={this.handleFeedbackForm}>
                                    <div className={`alert fade d-none alert2 ${this.state.alertClass}`}>{this.state.alertText}</div>
                                    <p>Rate the restaurant service between 1-10.</p>
                                    <div className="number-arr">
                                        <span data-count="1">1</span>
                                        <span data-count="2">2</span>
                                        <span data-count="3">3</span>
                                        <span data-count="4">4</span>
                                        <span data-count="5">5</span>
                                        <span data-count="6">6</span>
                                        <span data-count="7">7</span>
                                        <span data-count="8">8</span>
                                        <span data-count="9">9</span>
                                        <span data-count="10">10</span>
                                    </div>

                                    <textarea className="form-control" rows={6} placeholder="write down customer experience about restaurant..." onKeyUp={(event) => { this.setState({comment: event.target.value}) }}></textarea>
                                    <div className="file-uploader">
                                        <input type="file" onChange={this.handleUploadFile} />
                                        <label><i className="ti-file"></i> <span>Upload File</span></label>
                                    </div>

                                    <button type="submit" className="btn btn-block btn-primary radius-oval w-100 mt-4"><i className="ti-comment"> </i>Submit Feedback</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="modal fade slide" id="view-feedback">
                    <div className="modal-dialog">
                        <div className="modal-content px-4 py-2">
                            <div className="modal-header border-bottom-0">
                                <h4 className="modal-title">View Feedback</h4>
                                <button type="button" className="btn-close" data-bs-dismiss="modal" 
                                aria-label="Close"></button>
                            </div>
                            <div className="modal-body view-body">
                                <form onSubmit={this.handleFeedbackForm}>
                                    <p className="mb-0">Rating by customers</p>
                                    <h4 className="mb-3">{this.state.latestRating} / 10</h4>
                                    <p className="mb-0">Comment</p>
                                    <h4 className="mb-3">{this.state.latestComment}</h4>
                                    <p className="mb-0">Image</p>
                                    <img src={this.state.latestImage} alt={(this.state.latestImage === '') ? '' : 'No Image Available'} className="p-4 img-thumbnail w-75" />
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
