import React, { Component } from 'react'
import axios from  'axios'

import './index.css';
export default class Admin extends Component {
    state = {
        userID: '',
        password: '',
        userData: []
    }
    handleLogin = e => {
        e.preventDefault();

        var data = {
            username:this.state.userID,
            password: this.state.password
        };
        axios.post('http://18.223.252.52:5000/api/login', data)
        .then((response) => {
            if(response.status){
                setTimeout( () => {
                    window.location.href='/admin/dashboard'; 
                }, 1000)
            } 
        })
        .catch(err => console.log(err));
    }

    handleUsername(e){
        var values = e.target.value;
        this.setState({ userID: values})
    }
    handlePassword(e){
        var values = e.target.value;
        this.setState({ password: values})
    }

    render() {
        return (
            <div className="container">
                <h5 className="py-3">Remmsh<b className="text-primary">CRM</b></h5>
                <div className="row align-items-start">
                    <div className="col-md-6">
                        <div className="rs-background">
                            <img src="/assets/images/admin-login.jpg" className="img-fluid" alt="" />
                        </div>
                    </div> 
                    <div className="col-md-5">
                        <form className="login--form" onSubmit={this.handleLogin}>
                            <h2 className="font-weight-lighter mb-2 fw-light">Welcome Back <b>ADMIN!</b></h2>
                            <p className="text-muted">It is established fact that a reader will be distracted by the readable content of a page when looking at its layout.</p>

                            {/* <div className="alert alert-danger fade show d-flex align-items-center justify-content-between">
                                <p className="mb-0">Something went wrong. please try again later! </p>
                                <button className="close-btn bg-transparent border-0 h2 m-0 p-0" data-bs-dismiss="alert">&times;</button></div> */}

                            <div className="rs-input-groups mt-5">
                                <i className="ti-email"></i>
                                <div>
                                    <label className="small mb-0">Username</label>
                                    <input type="text" placeholder="Username or Email" className="form-control px-0 border-0 bg-transparent"  onChange={this.handleUsername.bind(this)} />
                                </div>
                                <span className="input-status"></span>
                            </div>
                            <div className="rs-input-groups">
                                <i className="ti-unlock"></i>
                                <div>
                                    <label className="small mb-0">Password</label>
                                    <input type="password" placeholder="*******" onChange={this.handlePassword.bind(this)} className="form-control px-0 border-0 bg-transparent"   />
                                </div>
                                <span className="input-status"></span>
                            </div>  

                            <div className="d-flex justify-content-between">
                                <div className="d-flex">
                                    <input type="radio" className="form-check me-2" />
                                    <label>Remember Me</label>
                                </div>
                                <a href="/admin/dashboard" className="p-0 text-decoration-none ls-2">Forgot passowrd?</a>
                            </div>
                            <button type="submit" className="btn btn-primary my-3 py-2 px-5 text-uppercase fw-bold">Login </button>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}
