import React from 'react'
import {BrowserRouter as Router, Switch, Route} from  'react-router-dom'
import Admin from './admin/index'
import Restaurant from './restaurant/index'
import Welcome from './welcome/index'
import RestaurantDashboard from './restaurant/dashboard'
import SearchReservation from './restaurant/SearchReservation'
import Customers from './restaurant/customers'
import CustomerReservation from './restaurant/customerReservation'
import Feedback from './restaurant/feedback'
import AdminDashboard from './admin/dashboard'
import AddRestaurant from './admin/restaurant'
import RestaurantList from './admin/restaurantList'
import AddReservation from './admin/reservation'
import AddFeedback from './admin/feedback'
import ReservationList from './admin/reservationList'
import ViewReservation from './admin/ViewReservation'


export default class Routing extends React.Component {
    render() {
        return (
            
            <Router>
                <Switch>
                    <Route path="/" exact>
                        <Welcome />            
                    </Route>
                    <Route path="/login/admin">
                        <Admin />
                    </Route>
                    <Route path="/login/restaurant">
                        <Restaurant />
                    </Route>
                    <Route path="/restaurant/dashboard">
                        <RestaurantDashboard />
                    </Route>
                    <Route path="/restaurant/search-reservation">
                        <SearchReservation />
                    </Route>
                    <Route path="/restaurant/customers">
                        <Customers />
                    </Route>
                    <Route path="/restaurant/customer-reservation">
                        <CustomerReservation />
                    </Route>
                    <Route path="/restaurant/feedback">
                        <Feedback />
                    </Route>
                    <Route path="/admin/dashboard">
                        <AdminDashboard />
                    </Route>
                    <Route path="/admin/restaurant/add">
                        <AddRestaurant />
                    </Route>
                    <Route path="/admin/restaurants">
                        <RestaurantList />
                    </Route>
                    <Route path="/admin/reservation/add">
                        <AddReservation />
                    </Route>
                    <Route path="/admin/reservation/feedback">
                        <AddFeedback />
                    </Route>
                    <Route path="/admin/reservations">
                        <ReservationList />
                    </Route>
                    <Route path="/admin/restaurant-selection">
                        <ViewReservation />
                    </Route>
                    
                    
                </Switch>
            </Router>
        )
    }
}
