import React, { Component } from 'react'
import axios from 'axios'
import $ from 'jquery'

export default class Restaurant extends Component {
    state = {
        username:'' ,
        password: '',
        alertText: '',
        alertClass: 'alert-success'
    }
    componentDidMount(){
        if(localStorage.getItem('restaurant') !== null){
            window.location.href= '/restaurant/dashboard'
        }
    }
    handleSubmit = (e) => {
        e.preventDefault();
        var un = this.state.username;
        var pw = this.state.password;
        axios.post('http://18.223.252.52:5000/api/verify-restaurant', {username: un, password: pw})
        .then( res => {
            if(res.data.status){
                this.setState({ alertText: res.data.message });
                $('.alert').addClass('show');
                localStorage.setItem('restaurant', res.data.name[0].restaurantName);
                setTimeout(() => {
                    $('.alert').removeClass('show'); 
                    this.setState({ alertText: '' });
                    window.location.href = '/restaurant/dashboard'
                }, 1500);
            }else{
                this.setState({ alertText: res.data.message, alertClass: 'alert-danger' });
                $('.alert').addClass('show'); 
                setTimeout(() => {
                    this.setState({ alertText: '' });
                    document.querySelector('form').reset()
                    $('.alert').removeClass('show'); 
                }, 2500);
            }
        })
    }

    handleUsername = e =>{
        this.setState({username: e.target.value})
    }
    handlePassword = e =>{
        this.setState({password: e.target.value})
    }

    render() {
        return (
            <div className="container">
                <h5 className="py-3">Remmsh<b className="text-primary">CRM</b></h5>
                <div className="row align-items-start">
                    <div className="col-md-6">
                        <div className="rs-background">
                            <img src="/assets/images/restaurant_1.png" className="img-fluid" alt="" />
                        </div>
                    </div>
                    <div className="col-md-5">
                        <form className="login--form" onSubmit={this.handleSubmit}>
                            <h2 className="font-weight-lighter mb-2 fw-light">Welcome Back <b>Restaurant!</b></h2>
                            <p className="text-muted">It is established fact that a reader will be distracted by the readable content of a page when looking at its layout.</p>

                            <div className={`alert fade ${this.state.alertClass}`}>{this.state.alertText}</div>

                            <div className="rs-input-groups mt-3">
                                <i className="ti-email"></i>
                                <div>
                                    <label className="small mb-0">Username</label>
                                    <input type="text" placeholder="Username or Email" className="form-control px-0 border-0 bg-transparent"  onKeyUp={this.handleUsername} />
                                </div>
                                <span className="input-status"></span>
                            </div>
                            <div className="rs-input-groups">
                                <i className="ti-unlock"></i>
                                <div>
                                    <label className="small mb-0">Password</label>
                                    <input type="password" placeholder="*******" className="form-control px-0 border-0 bg-transparent"  onKeyUp={this.handlePassword} />
                                </div>
                                <span className="input-status"></span>
                            </div>  

                            <div className="d-flex justify-content-between">
                                <div className="d-flex">
                                    <input type="radio" className="form-check me-2" />
                                    <label>Remember Me</label>
                                </div>
                                {/* <a href="/restaurant/dashboard" className="p-0 text-decoration-none ls-2">Forgot passowrd?</a> */}
                            </div>
                            <button type="submit" className="btn btn-success my-3 py-2 px-5 text-uppercase fw-bold">Login </button>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}
