import React, { Component } from 'react'
import AdminHeader from "./AdminHeader"
import AdminSidebar from "./AdminSidebar"
import {Link} from 'react-router-dom'
import axios from 'axios'
import './index.css'

export default class RestaurantList extends Component {
    state = {
        restaurants : []
    }

    componentDidMount(){
        axios.get('http://18.223.252.52:5000/api/restaurants')
        .then(res => {
            this.setState({ restaurants: res.data.data})
        })
    }
    render() {
        return (
            <div>
                <AdminHeader />
                <AdminSidebar activeLink="restaurants" />
                <div className="rs-content">
                    <div className="d-flex justify-content-between align-items-center">
                        <h4 className="form-title">Restaurants Listing</h4>
                        <Link to="/admin/restaurant/add" class="shadow btn btn-outline-primary me-5 mt-4"><i className="ti-plus me-2"></i>Add New Restaurant</Link>
                    </div> 
                    <hr/>
                    <div className="container  pt-2 px-5 py-4">
                        <div className="table-responsive">
                            <table className="table mt-0 table-hover">
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Restaurant</th>
                                        <th>username</th>
                                        <th>password</th>
                                        <th>Tables</th>
                                        <th>Cuisine's</th>
                                        <th>Phone</th>
                                        <th>Email</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        (this.state.restaurants.length > 0) ?
                                        this.state.restaurants.map((el,index) => 
                                            <tr key={index}>
                                                <td>{index + 1}.</td>
                                                <td>{el.restaurantName}</td>
                                                <td>{el.username}</td>
                                                <td>{el.password}</td>
                                                <td>{el.tableCount}</td>
                                                <td>
                                                    {JSON.parse(el.cuisine).map(ell => 
                                                        <span className='btn btn-sm radius-oval fw-bold border border-dark bagde bg-warning  m-1'>{ell}</span>
                                                    )}
                                                </td> 
                                                
                                                <td>{el.phone}</td>
                                                <td>{el.email}</td>
                                            </tr>    
                                        )
                                        : <tr> <td colspan="8" className="text-center">Searching...</td></tr>
                                    }
                                </tbody>
                            </table>
                        </div>
                        {/* <div  className="d-flex justify-content-between">
                            <p>10 Record for 1 of 4</p>
                            <ul className="pagination">
                                <li className="page-item"><Link className="page-link" to="#">Previous</Link></li>
                                <li className="page-item"><Link className="page-link" to="#">1</Link></li>
                                <li className="page-item"><Link className="page-link" to="#">2</Link></li>
                                <li className="page-item"><Link className="page-link" to="#">3</Link></li>
                                <li className="page-item"><Link className="page-link" to="#">Next</Link></li>
                            </ul>
                        </div> */}
                    </div>
                </div>
            </div>
        )
    }
}
