import React, { Component } from 'react'
import AdminHeader from "./AdminHeader"
import AdminSidebar from "./AdminSidebar"
import axios from 'axios'
import './index.css'

export default class AddReservation extends Component {
    
    state = {
        allData: [],
        restaurants: [],
        cuisine: [],
        customerName: '',
        phone: '',
        selectedCuisine: '',
        selectedRestaurant: '',
        guestNo: '',
        reservationDate: '',
        reservationTime: '',
        preference: '',
        alertText: '',
        alertClass: 'alert-danger'
    }

    
    componentDidMount(){
        let dis = this;
        var rest = localStorage.getItem('selected_restaurant');
        this.setState({selectedRestaurant:  rest})
        var detail = [];
        axios.get('http://18.223.252.52:5000/api/restaurants')
        .then(res => {
            detail = res.data.data;
            dis.setState({ restaurants: detail})
            
            
            detail.map( el => {
                if(el.restaurantName === rest){
                    dis.setState({cuisine: JSON.parse(el.cuisine)});
                }
                return true;
            });
        }) 

    }

    handleCustomer = (e) => {
        this.setState({customerName: e.target.value})
    }
    handleGuest = (e) => {
        this.setState({guestNo: e.target.value})
    }
    handleDate = (e) => {
        this.setState({reservationDate: e.target.value})
    }
    handleTime = (e) => {
        let time = e.target.value.split(':');
        
        var format = "AM";
        if(time[0] > 12){
            time[0] -= 12;
            format = 'PM'
        }
        
        var timeString = time[0] + ":"+ time[1] + ' '+ format;
        alert(timeString)
        
        this.setState({reservationTime: timeString})
    }
    handleDish = (e) => {
        this.setState({selectedCuisine: e.target.value})
    }
    handlePhone = (e) => {
        this.setState({phone: e.target.value})
    }
    handlePreference = (e) => {
        this.setState({preference: e.target.value})
    }

    submitReservation = (e) => {
        e.preventDefault();
        axios.post('http://18.223.252.52:5000/api/addReservation', {
            restaurantName: this.state.selectedRestaurant,
            customerName: this.state.customerName,
            guestCount: this.state.guestNo,
            reservationDate: this.state.reservationDate,
            reservationTime: this.state.reservationTime,
            cuisine: this.state.selectedCuisine,
            phone: this.state.phone,
            preference: this.state.preference
        }).then( res => {
            if(res.status){
                this.setState({alertText: res.data.message, alertClass: 'alert-success'});
                setTimeout(()=> {
                    window.location.href ='/admin/reservations'
                },1500)
            }else{
                alert('Something went wrong. Please try again');
            }
        })
    }


    render() {
        return (
            <div>
                <AdminHeader />
                <AdminSidebar activeLink="reservation" />
                <div className="rs-content">
                    <h4 className="form-title">Add Reservation</h4>
                       <hr/>
                    <form className="container reservation-form" onSubmit={this.submitReservation}>
                        <div className={`alert fade d-none ${this.state.alertClass}`}>{this.state.alertText}</div>
                        <div className="row">
                            <div className="col-md-8 mb-4">  
                                <input type="text" value={this.state.selectedRestaurant} disabled className="form-control" />
                            </div>
                            {/* <div className="col-md-4 mb-4">  
                                <label>Reservation ID</label>
                                <input type="text" className="form-control" value="RS2452525252" disabled />
                            </div> */}
                        </div>
                        <div className="row">
                            <div className="col-md-4 mb-4">  
                                <label>Customer Name</label>
                                <input type="text" className="form-control" placeholder="e.g. John Doe" onKeyUp={this.handleCustomer} />
                            </div>
                            <div className="col-md-4 mb-4">  
                                <label>No of Guest</label>
                                <input type="text" className="form-control" placeholder="e.g. 4 "  onKeyUp={this.handleGuest} />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-4 mb-4">  
                                <label>Date</label>
                                <input className="form-control" type="date"  onChange={this.handleDate} />
                            </div>
                            <div className="col-md-4 mb-4">  
                                <label>Time</label>
                                <input className="form-control" type="time" onChange={this.handleTime} />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-4 mb-4">  
                                <label>Select Cuisine</label>
                                <select className="form-select" onChange={this.handleDish}>
                                    <option>choose any one </option>
                                    {   
                                        (this.state.cuisine.length !== 0) ?
                                        this.state.cuisine.map((el, index) => 
                                            <option value={el} key={index}>{el}</option>
                                        ): ''
                                    }
                                </select>
                            </div>
                            <div className="col-md-4 mb-4">  
                                <label>Phone Number</label>
                                <input type="text" className="form-control"  placeholder="e.g. +919394-343-343"  onKeyUp={this.handlePhone} />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-8 mb-3">  
                                <label>Any other preference</label>
                                <textarea className="form-control"  rows={4}  onKeyUp={this.handlePreference} />
                            </div>
                        </div>

                        <button type="submit" className="btn btn-primary rounded-30 shadow my-4 px-4 py-3"><i className="ti-plus small d-inline-block me-2"></i>Add Reservation</button>
                    </form>
                </div>
            </div>
        )
    }
}
