import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'
import $ from 'jquery'

import './index.css';
export default class SearchReservation extends Component {
    state = {
        reservations: [],
        alertClass: 'alert-danger',
        alertText: '',
        restaurant: localStorage.getItem('restaurant'),
        resID: ''
    }
    componentShould
    componentDidMount(){
        var resv = []
        var rest = this.state.restaurant;
        localStorage.removeItem('phone');
        localStorage.removeItem('customer');
        axios.get('http://18.223.252.52:5000/api/reservations')
        .then( res => {
            res.data.data.map( elem => {
                if(elem.restaurantName === rest){
                    resv.push(elem);
                }else{
                    resv = [...resv];
                }
                return true
            })
        })
        setTimeout(()=>{
            this.setState({ reservations: resv})

            $('.table tr').each(function(){
                var ph = $(this).find('td:nth-child(9)').text();
                var dis = $(this);
                axios.post('http://18.223.252.52:5000/api/customerByRestaurant', {phone: ph, restaurantName: rest})
                .then( res => {
                    dis.find('td:nth-child(7) a').text(res.data.data.length);
                })
            })
        },2000)
    }

    componentDidUpdate(){
        var resv = []
        var rest = localStorage.getItem('restaurant');
        axios.get('http://18.223.252.52:5000/api/reservations')
        .then( res => {
            res.data.data.map( elem => {
                if(elem.restaurantName === rest){
                    resv.push(elem);
                }else{
                    resv = [...resv];
                }
                return true
            })
            this.setState({ reservations: resv})
        })
        var rssID = this.state.resID;
        axios.post('http://18.223.252.52:5000/api/updateReservationStatus', {reservationID: rssID, status: 'confirmed'})
        .then( res => {
            this.setState({alertText: "Reservation status changed", alertClass: 'alert-success' })
            $('.alert').removeClass('d-none');
            setTimeout(function(){
                window.location.reload()
            },2000)
        })
        
    }

    handleFormData = e => {
        let data = e.target.value;
        $('#rs-reservation-list tbody > tr td:nth-child(9)').each(function() {
            var dis = $(this);
            if(dis.text() !== data && data !== ''){
                dis.parent().hide();
                $('.hidden-row').removeClass('d-none')
            }else{
                dis.parent().show();
                $('.hidden-row').addClass('d-none')
            }
        })
    }

    toggleStatus = e => {
        var resrevation_id = $(e.target).parent().parent('tr').attr('data-id');
        this.setState({resID: resrevation_id})
        axios.post('http://18.223.252.52:5000/api/updateReservationStatus', {reservationID: resrevation_id, status: 'confirmed'})
        .then( res => {
            this.setState({alertText: "Reservation status changed", alertClass: 'alert-success' })
            $('.alert').removeClass('d-none');
            setTimeout(function(){
                window.location.reload()
            },2000)
        })

        var rest = this.state.restaurant;
        var msg = `The Restaurant ${rest} has changed the status of a reservation.`
        axios.post('http://18.223.252.52:5000/api/addNotification', {user: 'admin', message: msg})
        .then(res => { console.log('notification sent.')})
    } 
    toggleStatus2 = e => {
        var resrevation_id = $(e.target).parent().parent('tr').attr('data-id');
        this.setState({resID: resrevation_id})
        axios.post('http://18.223.252.52:5000/api/updateReservationStatus', {reservationID: resrevation_id, status: 'Seated In'})
        .then( res => {
            
            if(res.data.status){
                this.setState({alertText: "Reservation status changed", alertClass: 'alert-success' })
                $('.alert').removeClass('d-none');
                setTimeout(function(){
                    window.location.reload()
                },2000)
            }
        })

        var rest = this.state.restaurant;
        var msg = `The Restaurant ${rest} has changed the status of a reservation.`
        axios.post('http://18.223.252.52:5000/api/addNotification', {user: 'admin', message: msg})
        .then(res => { console.log('notification sent.')})
    } 


    
    handlCancelReservation = e => {
        // e.stopPropogation()
        var resID = $(e.target).parent().parent('tr').data('id');
        axios.post('http://18.223.252.52:5000/api/activeReservation', { id: resID})
        .then(res => {
            if(res.data.status) {
                this.setState({alertText: "Reservation calncelled." })
                $('.alert').removeClass('d-none');
                setTimeout(function(){
                    var rest = localStorage.getItem('restaurant')
                    let msg = 'The Restaurant ' + rest + 'has cancelled a reservation.';
                    axios.post('http://18.223.252.52:5000/api/addNotification', {user: 'admin', message: msg})
                    .then(res => { console.log('notification sent.')})
                    window.location.reload()
                }, 2000);
            }
        })
    }

    handleForm2 = e => {
        var date = e.target.value;
        if(date === "today"){
            $('#rs-reservation-list tbody > tr td:nth-child(3)').each(function() {
                var dis = $(this);
                var dvalue = new Date($(this).text()).getDate(),
                    now = new Date().getDate();

                if(dvalue !== now  && date !== ''){
                    dis.parent().hide();
                    $('.hidden-row').removeClass('d-none')
                }else{
                    dis.parent().show();
                    $('.hidden-row').addClass('d-none')
                }
            })
        }
        else if(date === "tomorrow"){
            $('#rs-reservation-list tbody > tr td:nth-child(3)').each(function() {
                var dis = $(this);
                var dvalue = new Date($(this).text()).getDate(),
                    now = new Date().getDate() + 1;

                if(dvalue !== now  && date !== ''){
                    dis.parent().hide();
                    $('.hidden-row').removeClass('d-none')
                }else{
                    dis.parent().show();
                    $('.hidden-row').addClass('d-none')
                }
            })
        }
        else if(date === "this_month"){
            $('#rs-reservation-list tbody > tr td:nth-child(3)').each(function() {
                var dis = $(this);
                var dvalue = new Date($(this).text()).getMonth(),
                    now = new Date().getMonth();

                if(dvalue !== now  && date !== ''){
                    dis.parent().hide();
                    $('.hidden-row').removeClass('d-none')
                }else{
                    dis.parent().show();
                    $('.hidden-row').addClass('d-none')
                }
            })
        }
        else if(date === "last_month"){
            $('#rs-reservation-list tbody > tr td:nth-child(3)').each(function() {
                var dis = $(this);
                var dvalue = new Date($(this).text()).getMonth(),
                    now = new Date().getMonth() - 1;

                if(dvalue !== now  && date !== ''){
                    dis.parent().hide();
                    $('.hidden-row').removeClass('d-none')
                }else{
                    dis.parent().show();
                    $('.hidden-row').addClass('d-none')
                }
            })
        }
    }

    handleClearFilter = e => {
        $('#rs-reservation-list tbody > tr').each(function() {
            $(this).show();
            $('.hidden-row').hide();
            
        })
    }

    setValues = e => {
        localStorage.setItem('phone', $(e.target).parent('td').data('phone'))
        localStorage.setItem('customer', $(e.target).parent('td').data('cname'))
    }

    render() {
        return (
            <>
                <div className="container-fluid">
                        <Link to="/restaurant/dashboard" className="h4 rs-back-btn text-decoration-none my-3 d-inline-block">
                            <i className="ti-angle-left"></i>
                            Back
                        </Link>
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 mx-auto">
                            <h5 className="text-center display-4">Reservations</h5>
                            
                        </div>
                    </div>

                    <div className="row mt-5 mb-2">
                        <div className="d-flex align-items-center mb-4">
                            <div className="rs-search-inputs d-flex align-items-center border-1 border border-dark overflow-hidden">
                                <input type="text" className="form-control border-0" placeholder="Filter by Number..." onChange={this.handleFormData} />
                                <i className="ti-search"></i>
                            </div>
                            <div className="filter-select">
                                <select className="custom-select border-0" onChange={this.handleForm2}>
                                    <option value="today">Today</option>
                                    <option value="tomorrow">Tomorrow</option>
                                    <option value="this_month">This Month</option>
                                    <option value="last_month">Last Month</option>
                                </select>
                            </div>
                            <button className="btn btn-outline-secondary ms-2 ms-auto" onClick={this.handleClearFilter}><i className="ti-filter small"></i> clear filter </button>
                        </div>
                        <div className={`alert d-none ${this.state.alertClass}`}>{this.state.alertText}</div>
                        <div className="table-responsive">
                            <table className="table table-hover" id="rs-reservation-list">
                                <thead className="border-top border-dark">
                                    <tr>
                                        <th>#</th>
                                        <th>Customer Name</th>
                                        <th style={{width: '100px'}}>Date</th>
                                        <th style={{width: '80px'}}>Time</th>
                                        <th style={{width: '100px'}}>No of Guests</th>
                                        <th style={{width: '120px'}}>Cuisine</th>
                                        <th style={{width: '120px'}}>Visits</th>
                                        <th style={{width: '220px'}}>Preferences</th>
                                        <th style={{width: '120px'}}>Phone</th>
                                        <th style={{width: '120px'}}>Status</th>
                                        <th>Action</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        (this.state.reservations.length > 0) ?
                                        this.state.reservations.map((el, index) => 
                                            <tr key={index} data-id={el._id}>
                                                <td>{index + 1}</td>
                                                <td>{el.customerName}</td>
                                                <td>{el.reservationDate.split('T')[0]}</td>
                                                <td>{el.reservationTime}</td>
                                                <td>{el.guestCount}</td>
                                                <td>{el.cuisine}</td>
                                                <td data-cname={el.customerName} data-phone={el.phone}><Link to="/restaurant/customer-reservation" onClick={this.setValues}></Link></td>
                                                <td>{el.preferences}</td>
                                                <td>{el.phone}</td>
                                                {
                                                    (el.status === "waiting") 
                                                    ? 
                                                    <td>
                                                        <span 
                                                            className="btn btn-sm shadow btn-warning" 
                                                            onClick={this.toggleStatus}
                                                        >{el.status}
                                                        </span>
                                                    </td> 
                                                    : 
                                                    (el.status === "confirmed") 
                                                    ? 
                                                    <td>
                                                        <span 
                                                            className="btn btn-sm shadow btn-success" 
                                                            onClick={this.toggleStatus2}
                                                        >{el.status}
                                                        </span>
                                                    </td> 
                                                    : 
                                                    (el.status === "Seated In")
                                                    ?
                                                    <td>
                                                        <span 
                                                            className="btn btn-sm shadow bg-violet text-white"
                                                        >{el.status}
                                                        </span>
                                                    </td>
                                                    :
                                                    <td>
                                                        <span 
                                                            className="btn btn-sm shadow btn-danger"
                                                        >{el.status}
                                                        </span>
                                                    </td>
                                                }
                                                
                                                <td>
                                                    {
                                                        (el.isActive) ? 
                                                        <span 
                                                            className="btn btn-sm shadow btn-danger" 
                                                            onClick={this.handlCancelReservation}
                                                        >Cancel
                                                        </span>
                                                        : 
                                                        <span 
                                                            className="btn btn-sm shadow btn-secondary"
                                                            disabled
                                                        >Cancelled
                                                        </span>
                                                    }
                                                </td>
                                            </tr>
                                        ) :  <tr><td colSpan="11" className="text-center py-5"><i className="ti-face-smile  fs-5"></i><p className="lead fw-bold mb-0">Searching Reservations...</p></td></tr>
                                    }
                                     <tr className="hidden-row d-none"><td colSpan="11" className="text-center py-5"><i className="ti-face-sad  fs-5"></i><p className="lead fw-bold mb-0">0 results found.</p></td></tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </>
        )
    }
}
