import React, { Component } from 'react'
import AdminHeader from "./AdminHeader"
import AdminSidebar from "./AdminSidebar"
import './index.css'

export default class AddFeedback extends Component {
    render() {
        return (
            <div>
                <AdminHeader />
                <AdminSidebar activeLink="feedback" />
                <div className="rs-content">
                    <h4 className="form-title">Send Feedback</h4>
                    <hr/>
                    <form className="container reservation-form">
                        <div className="row">
                            <div className="col-md-4 mb-4">  
                                <label>Enter Reservation ID</label>
                                <input type="text" className="form-control" />
                            </div>
                        </div>
                        
                        <div className="row">
                            <div className="col-md-8 mb-3">  
                                <label>Feedback</label>
                                <textarea className="form-control"  rows={4} />
                            </div>
                        </div>

                        <button className="btn btn-primary rounded-30 shadow my-4 px-4 py-3"><i className="ti-comment small d-inline-block me-2"></i>Send feedback</button>
                    </form>
                </div>
            </div>
        )
    }
}
