import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'
import $ from 'jquery'
import './index.css';
import './feedback.css';
export default class ReservationFeedback extends Component {
    state = {
        feedbacks: []
    }

    componentDidMount(){
        var resv = []
        var dis = this;
        axios.get('http://18.223.252.52:5000/api/feedbacks')
        .then( res => {
            res.data.data.map( elem => {
                var rest = localStorage.getItem('restaurant');

                if(elem.restaurantName === rest){
                    resv.push(elem);
                }
            })
            dis.setState({ feedbacks: resv})
        })
        
        
        
    }

    handleFormData = e => {
        let data = e.target.value;
        $('.table tr td:nth-child(3)').each(function() {
            var dis = $(this);
            if(dis.text() !== data && data !== ''){
                dis.parent().hide();
                $('.hidden-row').removeClass('d-none')
            }else{
                dis.parent().show();
                $('.hidden-row').addClass('d-none')
            }
        })
    }
    
    render() {
        return (
            <>
                <div className="container-fluid">
                        <Link to="/restaurant/dashboard" className="h4 rs-back-btn text-decoration-none my-3 d-inline-block">
                            <i className="ti-angle-left"></i>
                            Back
                        </Link>
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 mx-auto">
                            <h1 className="text-center fw-light mb-0">Customer Feedback</h1>
                            <p className="text-center">Enter customer phone number</p>
                        </div>
                    </div>
                    <div className="rs-search-inputs d-flex align-items-center border-1 border border-dark overflow-hidden mb-2 ms-auto">
                        <input type="text" className="form-control form-control-lg border-0" placeholder="Enter Phone Number..." onKeyUp={this.handleFormData} />
                        <i className="ti-search"></i>
                    </div>
                    <table className="table table-hover">
                        <thead className="border-top border-dark">
                            <tr>
                                <th>#</th>
                                <th>Customer Name</th>
                                <th>Phone</th>
                                <th>Date</th>
                                <th>Time</th>
                                <th>Rating</th>
                                <th>Comment</th>
                                <th>Image</th>
                            </tr>
                        </thead>
                        <tbody>
                            {   (this.state.feedbacks.length > 0) ?
                                this.state.feedbacks.map((el, index) => 
                                    <tr key={el._id}>
                                        <td>{index + 1}</td>
                                        <td>{el.customerName}</td>
                                        <td>{el.phone}</td>
                                        <td>{el.reservationDate.split('T')[0]}</td>
                                        <td>{el.reservationTime}</td>
                                        <td>{el.rating}/19</td>
                                        <td>{el.comment}</td>
                                        <td><img src={el.image} alt="" style={{borderRadius: 5, maxWidth: 100}} className="img-fluid border" /></td>
                                    </tr>
                                )
                                : <tr ><td colSpan="9" className="text-center py-5"><i className="ti-smile-face  fs-5"></i><p className="lead fw-bold mb-0">Searching results.</p></td></tr>
                            }
                            <tr className="hidden-row d-none"><td colSpan="9" className="text-center py-5"><i className="ti-face-sad  fs-5"></i><p className="lead fw-bold mb-0">0 results found.</p></td></tr>
                        </tbody>
                        
                    </table>
                </div>
            </>
        )
    }
}
