import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'

export default class AdminHeader extends Component {
    state = {
        notification: [],
        nlength: 0
    }

    componentDidMount(){
        axios.post('http://18.223.252.52:5000/api/getNotification', { user:'admin'})
            .then(res => this.setState({notification: res.data.data, nlength: res.data.data.length}));
        }
        
    componentDidUpdate(){
        axios.post('http://18.223.252.52:5000/api/getNotification', { user:'admin'})
            .then(res => this.setState({notification: res.data.data, nlength: res.data.data.length}));
    }
    render() {
        return (
            <header className="fixed-top bg-white shadow">
                <div className="container-fluid">
                    <div className="row justify-content-between align-items-center py-2 border-bottom">
                        <div className="col-6">
                            <Link to="admin/dashboard" className="text-decoration-none text-secondary"><h5 className="mb-0">Remmsh<b className="text-primary">CRM</b></h5></Link>
                        </div>
                        <div className="col-6">
                            <div className="btn-group float-end">

                                <button type="button" className="btn dropdown-toggle fw-bold" data-bs-toggle="dropdown" data-bs-display="dynamic" aria-expanded="false">
                                    <i className="ti-bell me-2"><sup className="badge bg-violet radius-oval">{this.state.nlength}</sup></i>Notication 
                                </button>
                                <ul className="dropdown-menu dropdown-menu-end">
                                    {
                                        (this.state.notification.length > 0) ?
                                        this.state.notification.map((el) => 
                                            <li className="dropdown-item"> <p className="mb-0">{el.message}</p></li>
                                        ) : <li className="dropdown-item"> <p className="mb-0">No notification.</p></li>
                                    }
                                </ul>
                                
                                <button type="button" className="btn dropdown-toggle fw-bold" data-bs-toggle="dropdown" data-bs-display="static" aria-expanded="false">
                                    <i className="ti-user me-2"></i>Admin
                                </button>
                                <ul className="dropdown-menu dropdown-menu-end">
                                    <li><button className="dropdown-item" type="button"><i className="ti-settings me-3"></i>Profile</button></li>
                                    <li><button className="dropdown-item" type="button"><i className="ti-power-off me-3"></i>Logout</button></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </header>
        )
    }
}
