import React, { Component } from 'react'
import AdminHeader from "./AdminHeader"
import AdminSidebar from "./AdminSidebar"
import axios from 'axios'
import $ from 'jquery'

import './index.css'

export default class ViewReservation extends Component {
    state = {
        reservations : [],
        restaurants: []
    }

    componentDidMount(){
        axios
        .get('http://18.223.252.52:5000/api/restaurants')
        .then( res => {
            this.setState({ restaurants: res.data.data });
        })
        localStorage.removeItem('selected_restaurant')
    }

    showReservation = e => {
        var rsID = $(e.target).data('name')
        localStorage.setItem('selected_restaurant', rsID)
        window.location.href="/admin/reservations"
    }

    render() {
        return (
            <div>
                <AdminHeader />
                <AdminSidebar activeLink="reservation" />
                <div className="rs-content">
                    <h4 className="form-title text-uppercase">Restaurants</h4>
                        <div className="restaurant-wrapper">
                            <div class="row">
                            {
                                this.state.restaurants.map(el => 
                                    <div className="col-md-4">
                                        <div className="restaurantBox">
                                            <div>Restaurant Name</div>
                                            <h5 className="fw-bold mb-3">{el.restaurantName}</h5>
                                            <div>Tables Available</div>
                                            <h5 className="fw-bold mb-3">{el.tableCount}</h5>
                                            <div>Email Address</div>
                                            <h5 className="fw-bold mb-3">{el.email}</h5>
                                            <div>Phone</div>
                                            <h5 className="fw-bold mb-3">{el.phone}</h5>
                                            <button 
                                                className='btn btn-primary radius-oval' 
                                                data-name={el.restaurantName}
                                                onClick={this.showReservation}>View Reservations</button>
                                        </div>
                                    </div>
                                )
                            }
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
