import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'

export default class RestaurantHeader extends Component {
    state = {
        RestaurantName: this.props.resName,
        notification: [],
        nlength: 0
    }

    componentDidMount(){
        var rest = this.state.RestaurantName;
        axios.post('http://18.223.252.52:5000/api/getNotification', { user:this.state.rest})
            .then(res => this.setState({notification: res.data.data, nlength: res.data.data.length}));
        }
        
    componentDidUpdate(){
        var rest = this.state.RestaurantName;
        axios.post('http://18.223.252.52:5000/api/getNotification', { user:rest})
            .then(res => this.setState({notification: res.data.data, nlength: res.data.data.length}));
    }

    handleLogout = (e) => {
        localStorage.removeItem('restaurant')
        window.location.href='/login/restaurant'
    }
    render() {
        return (
            <header>
                <div className="container">
                    <div className="row justify-content-between my-4">
                        <div className="col-md-4">
                            <Link to="/restaurant/dashboard" className="text-decoration-none text-secondary"><h4 className="mb-0">Remmsh<b className="text-primary">CRM</b></h4></Link>
                        </div>
                        <div className="col-md-4">
                            <div className="btn-group float-end">
                                <button type="button" className="btn dropdown-toggle fw-bold" data-bs-toggle="dropdown" data-bs-display="static" aria-expanded="false">
                                    <i className="ti-bell me-2"><sup>{this.state.nlength}</sup></i>Notification
                                </button>
                                <ul className="dropdown-menu dropdown-menu-end">
                                    {
                                       (this.state.notification.length > 0) ?
                                       this.state.notification.map((el) => 
                                           <li className="dropdown-item"> <p className="mb-0">{el.message}</p></li>
                                       ) : <li className="dropdown-item"> <p className="mb-0">No notification.</p></li>
                                    }
                                </ul>
                                <button type="button" className="btn dropdown-toggle fw-bold" data-bs-toggle="dropdown" data-bs-display="static" aria-expanded="false">
                                    <i className="ti-home me-2"></i>{this.state.RestaurantName}
                                </button>
                                <ul className="dropdown-menu dropdown-menu-end">
                                    {/* <li><button className="dropdown-item" type="button"><i className="ti-settings me-3"></i>Profile</button></li> */}
                                    <li><button className="dropdown-item" type="button" onClick={this.handleLogout}><i className="ti-power-off me-3"></i>Logout</button></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </header>
        )
    }
}
