import React, { Component } from 'react'
import { Link } from  'react-router-dom'
export default class Welcome extends Component {
    render() {
        return (
            <div>
                <div className="d-flex justify-content-center my-5 py-5">
                    <Link to="/login/admin" className="border p-5 display-5 text-danger text-decoration-none">
                        <i className="ti-user d-block text-center"></i>Admin Login</Link>
                    <Link to="/login/restaurant" className="border p-5 display-5 text-decoration-none text-secondary">
                        <i className="ti-star d-block text-center"></i>
                        Restaurant Login</Link>
                </div>
            </div>
        )
    }
}
