import React, { Component } from 'react'
import AdminHeader from "./AdminHeader"
import AdminSidebar from "./AdminSidebar"
import axios from 'axios'
import $ from 'jquery'
import './index.css'

export default class AddRestaurant extends Component {
    state = {
        restaurantName: '',
        tableCount: '',
        address1: '',
        address2: '',
        pincode: '',
        city: '',
        states: '',
        cuisine: [],
        email : '',
        phone: '',
        username: '',
        password: ''
        ,
        alertText: '',
        alertClass: 'alert-danger'
    }
    handleSubmit = e => {
        e.preventDefault();
        var data = {
            restaurantName: this.state.restaurantName,
            tableCount: this.state.tableCount,
            addressLine1: this.state.address1,
            addressLine2: this.state.address2,
            pincode: this.state.pincode,
            city: this.state.city,
            state: this.state.states,
            cuisine: this.state.cuisine,
            email : this.state.email,
            phone: this.state.phone,
            username: this.state.username,
            password: this.state.password
        },
        dis = this;
        axios.post('http://18.223.252.52:5000/api/addRestaurant', data)
        .then(res => {
            if(res.status){
                dis.setState({alertText: "Restaurant Added.", alertClass: 'alert-success'});
                $('.alert').removeClass('d-none');
                document.querySelector('form').reset()
                setTimeout(() => {
                    window.location.href = '/admin/restaurants'
                }, 1000);
            }
        })
    }

    handleResturantName(e){
        this.setState({ restaurantName: e.target.value })
    }
    handleTable(e){
        this.setState({ tableCount: e.target.value })
    }
    handleAddress1(e){
        this.setState({ address1: e.target.value })
    }
    handleAddress2(e){
        this.setState({ address2: e.target.value })
    }
    handlePincode(e){
        this.setState({ pincode: e.target.value })
    }
    handleCity(e){
        this.setState({ city: e.target.value })
    }
    handleState(e){
        this.setState({ states: e.target.value })
    }
    handlePhone(e){
        this.setState({ phone: e.target.value })
    }
    handleMail(e){
        this.setState({ email: e.target.value })
    }
    handleUsername(e){
        this.setState({ username: e.target.value })
    }
    handlePassword(e){
        this.setState({ password: e.target.value })
    }

    handleCuisine(e){
        var values = e.target.value.split(',');
        this.setState({ cuisine: JSON.stringify(values) })
    }

    render() {
        return (
            <div>
                <AdminHeader />
                <AdminSidebar activeLink="restaurant" />
                <div className="rs-content">
                    <h4 className="form-title">Add Restaurant</h4>
                    <hr/>
                    <form className="container reservation-form" onSubmit={this.handleSubmit}>
                        <div className={`alert d-none ${this.state.alertClass}`}>{this.state.alertText}</div>
                        <div className="row">
                            <div className="col-md-8 mb-4">  
                                <label>Restaurant Name</label>
                                <input type="text" className="form-control" onKeyUp={this.handleResturantName.bind(this)} />
                            </div>
                            
                            <div className="col-md-4 mb-4">  
                                <label>No. of table available</label>
                                <input type="text" className="form-control" placeholder="e.g. 5 tables" onKeyUp={this.handleTable.bind(this)} />
                            </div>
                            <div className="col-md-12 mb-4">  
                                <label>Address</label>
                                <div className="row">
                                    <div className="col-md-4 mb-4">
                                        <input type="text" className="form-control" placeholder="Address Line 1" onKeyUp={this.handleAddress1.bind(this)} />
                                    </div>
                                    <div className="col-md-4 mb-4">
                                        <input type="text" className="form-control" placeholder="Address Line 2" onKeyUp={this.handleAddress2.bind(this)} />
                                    </div>
                                    <div className="col-md-4 mb-4">
                                        <input type="text" className="form-control" placeholder="Pincode" onKeyUp={this.handlePincode.bind(this)} />
                                    </div>
                                    <div className="col-md-4 mb-4">
                                        <input type="text" className="form-control" placeholder="City" onKeyUp={this.handleCity.bind(this)} />
                                    </div>
                                    <div className="col-md-4 mb-4">
                                        <input type="text" className="form-control" placeholder="State"
                                        onKeyUp={this.handleState.bind(this)}/>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-4 mb-4">  
                                <label>Phone</label>
                                <input className="form-control" type="text" onChange={this.handlePhone.bind(this)} />
                            </div>
                            <div className="col-md-4 mb-4">  
                                <label>Email Address</label>
                                <input className="form-control" type="email" onKeyUp={this.handleMail.bind(this)} />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-8 mb-3">  
                                <label>Add Cuisine  (seperate each cuisine with ','</label>
                                <textarea className="form-control"  rows={4} onKeyUp={this.handleCuisine.bind(this)} />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-4 mb-4">  
                                <label>Username</label>
                                <input className="form-control" type="text" onKeyUp={this.handleUsername.bind(this)} />
                            </div>
                            <div className="col-md-4 mb-4">  
                                <label>Password</label>
                                <input className="form-control" type="password" onKeyUp={this.handlePassword.bind(this)} />
                            </div>
                        </div>

                        <button className="btn btn-primary rounded-30 shadow my-4 px-4 py-3"><i className="ti-plus small d-inline-block me-2"></i>Add Restaurant</button>
                    </form>
                </div>
            </div>
        )
    }
}
