import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'
import './index.css'
import $ from 'jquery'

import './index.css';
export default class Customers extends Component {
    state = {
        numbers: [],
        customers: []
    }

    componentDidMount(){
        var dis = this, numList = [], list = [];
        var rest = localStorage.getItem('restaurant');
        axios.get('http://18.223.252.52:5000/api/getCustomers')
        .then( res => {
            res.data.data.map( elem => {
                numList.push(elem)
                return true
            })

            dis.setState({ numbers: numList});

            numList.map( no => {
                axios.post('http://18.223.252.52:5000/api/customerByRestaurant', {phone: no, restaurantName: rest})
                .then( res => {
                    
                    res.data.data.map( elem => {
                        if(res.data.status){
                            list.push(elem)
                        }
                        return true
                    })
                    return true
                })
                return true
            })
            setTimeout(() => {
                var dataArr = list.map(item=>{
                    return [item.phone,item]
                }); 
                var maparr = new Map(dataArr);

                var result = [...maparr.values()];

                dis.setState({ customers: result })

                $('.table tr').each(function(){
                    var ph = $(this).find('td:nth-child(3)').text();
                    var dis = $(this);
                    axios.post('http://18.223.252.52:5000/api/customerByRestaurant', {phone: ph, restaurantName: rest})
                    .then( res => {
                        dis.find('td:nth-child(4)').text(res.data.data.length);
                    })
                })
            },1000)
            return true
        })
    }

    
    render() {
        return (
            <>
                <div className="container-fluid">
                        <Link to="/restaurant/dashboard" className="h4 rs-back-btn text-decoration-none my-3 d-inline-block">
                            <i className="ti-angle-left"></i>
                            Back
                        </Link>
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 mx-auto">
                            <h5 className="text-center display-4">Customers</h5>
                        </div>
                    </div>

                    <div className="row mt-5 mb-2">
                        <div className="table-responsive">
                            <table className="table table-hover" id="rs-reservation-list">
                                <thead className="border-top border-dark">
                                    <tr>
                                        <th>#</th>
                                        <th>Customer Name</th>
                                        <th>Phone</th>
                                        <th>No. of Visit</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        (this.state.customers.length > 0) ?
                                        this.state.customers.map((el, index) => 
                                            <tr key={index} data-id={el._id}>
                                                <td>{index + 1}</td>
                                                <td>{el.customerName}</td>
                                                <td>{el.phone}</td>
                                                <td></td>
                                            </tr>
                                        ) : <tr><td colSpan="9" className="text-center py-5"><i className="ti-face-smile  fs-5"></i><p className="lead fw-bold mb-0">Searching results.</p></td></tr>
                                    }
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </>
        )
    }
}
